.search-form {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 10px; /* Adds spacing between input fields */
  flex-wrap: wrap; /* Ensures responsiveness on smaller screens */
}

.search-form input {
  padding: 10px;
  width: 200px;
  border: none;
  border-radius: 5px;
}

.search-form button {
  padding: 10px 20px;
  background-color: #ff6600;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
